<template>
  <v-app>
    <v-app-bar color="transparent" elevation="0" class="pa-xl-10 pa-lg-10 pa-4" absolute hide-on-scroll>
      <div class="d-flex align-center text-xl-h4 text-lg-h4 text-md-h3 text-sm-h3 text-h6 font-weight-light white--text">
        <span class="navBrand">./savano</span>
      </div>

      <v-spacer></v-spacer>

      <!-- <v-btn
        href="https://github.com/vuetifyjs/vuetify/releases/latest"
        target="_blank"
        class="white--text"
        text
      >
        <span class="text-lg-h5 text-xl-h5 text-subtitle-2 mr-2">Latest Release</span> 
        <v-icon class="text-md-h3 text-sm-h3">mdi-open-in-new</v-icon>
      </v-btn> -->
    </v-app-bar>
    <v-btn
      v-scroll="onScroll"
      v-show="fab"
      fab
      dark
      fixed
      bottom
      right
      color="red lighten-1"
      @click="toTop"
    >
      <v-icon>mdi-arrow-up</v-icon>
    </v-btn>

    <v-main>
      <router-view></router-view>
    </v-main>
  </v-app>
</template>

<script>

export default {
  name: 'App',
  data: () => ({
    fab: false
  }),
  methods: {
    onScroll (e) {
      if (typeof window === 'undefined') return
      const top = window.pageYOffset || e.target.scrollTop || 0
      this.fab = top > 560
    },
    toTop () {
      this.$vuetify.goTo(0)
    }
  }
}
</script>
<style lang="scss" scoped>
.navBrand {
  letter-spacing: 2px!important;
}
</style>
